<template>
  <div id="login">
    <h1>泡泡商城后台管理系统</h1>
    <div class="components-input-demo-presuffix con1">
      <a-input ref="userNameInput" v-model="username" placeholder="用户名">
        <a-icon slot="prefix" type="user" :style="{ fontSize: '14px' }" />
      </a-input>
      <br />
      <br />
      <a-input ref="passwordInput" v-model="password" placeholder="密码">
        <a-icon type="key" slot="prefix" :style="{ fontSize: '14px' }" spin />
      </a-input>
      <a-button type="primary" class="btn" @click="loginAction">登录</a-button>
    </div>
  </div>
</template>

<script>
import { Input, Icon,Button } from "ant-design-vue";
import axios from "axios";
export default {
  components: {
    [Input.name]: Input,
    [Icon.name]: Icon,
    [Button.name]: Button,
  },
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    // 登录
    loginAction() {
      axios
        .post("/pc/api/user/login", {
          username: this.username,
          password: this.password,
        })
        .then((data) => {
          console.log(data);
          if (data.data.code === 0) {
            //登录成功
            this.$store.dispatch("user/changeLoginAction", true);
            this.$router.push({ name: "home" });
            // 查询用户信息
            this.$store.dispatch("user/requestUserInfo");
          } else {
            // 登录失败
            alert(data.data.message);
          }
        })
        .catch((error) => {
          console.log(error);
          alert("登录失败");
        });
    },
  },
};
</script>

